import {Component, Inject} from '@angular/core';
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {BaseUpsertComponentV3} from "../../../../../../../base/components/base-upsert-v3.component";
import {defaultEmployeeConfig, EmployeeConfig, selfEndpoints, selfRoutes} from "../../models/config.model";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {EmployeeService} from "../../services/employee.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {MAT_DIALOG_DATA} from "@angular/material/dialog";
import {BreadcrumbItem} from "../../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {HttpErrorResponse} from "@angular/common/http";
import {IImageSlice} from "../../../../../../common/modules/cms/files/models/file.model";
import {BaseConfigV2} from "../../../../../../../base/models/baseConfig";
import {IEmployeeCreateReq, IEmployeeDetail, IEmployeeUpdateReq} from "../../models/employee.model";
import {IOrganization} from "../../../../organizations/models/organization.model";
import {CheckUsernameResponse, IUserBrief} from "../../../../users/users/models/user.model";
import {OrganizationService} from "../../../../organizations/services/organization.service";
import {UserService} from "../../../../users/users/services/user.service";


@Component({
  selector: 'app-employee-upsert',
  templateUrl: './employee-upsert.component.html',
  styleUrls: ['./employee-upsert.component.scss']
})
export class EmployeeUpsertComponent extends BaseUpsertComponentV3<IEmployeeDetail> {

  config: EmployeeConfig = defaultEmployeeConfig

  selectedOrg: IOrganization | null = null
  selectedUser: IUserBrief | null = null

  employeeForm = new FormGroup({
    name: new FormControl('', [Validators.required, Validators.minLength(3)]),
    title: new FormControl('', [Validators.required, Validators.minLength(3)]),
    phone: new FormControl<string | null>(null, []),
    email: new FormControl<string | null>(null, [Validators.email]),
    description: new FormControl<string | null>(''),
    image: new FormControl<string | null>(null),
    organization_id: new FormControl<number>(-1),
    user_id: new FormControl<number>(-1),
    user: new FormGroup({
      // name: new FormControl('', [Validators.required]),
      username: new FormControl('', [Validators.required, Validators.minLength(3)]),
      // phone: new FormControl('', []),
      // email: new FormControl('', [Validators.email]),
      password: new FormControl('', [Validators.required, Validators.minLength(6), Validators.maxLength(30)]),
      role_names: new FormControl<string[]>([], [])
    })
  })

  isAdmin: boolean = false
  isUpdate: boolean = false
  checkUsernameResponse: CheckUsernameResponse | null = null

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              route: ActivatedRoute,
              private employeeService: EmployeeService,
              private orgService: OrganizationService,
              private userService: UserService,
              private notifier: NotifierService,
              authService: AuthService,
              @Inject(MAT_DIALOG_DATA) dialogData: any) {
    super(eventBus, router, subContainer, route, dialogData)

    if (authService.isAdmin()) {
      this.config.endpoint = selfEndpoints.adminEndpoint
      this.config.delEndpoint = selfEndpoints.adminEndpoint
      this.isAdmin = true
    }

    if (dialogData.title != null) {
      this.employeeForm.controls.title.setValue(dialogData.title)
    }

  }

  protected override componentName(): string {
    return EmployeeUpsertComponent.name
  }

  override ngOnInit() {
    super.ngOnInit();

    let key = this.componentKey(EventKey.ORGANIZATIONS.GET_MY_ORG())
    this.orgService.fetchMyOrgs(key)
    this.subscribe(key, null, (value, meta) => {
      this.selectedOrg = meta.data[0]
      this.employeeForm.controls.organization_id.setValue(
        this.selectedOrg == null ? -1 : this.selectedOrg.id
      )
    })

    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      console.log(error)
    });

  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
        text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "Employees",
        link: "/dash/employees",
        active: false
      },
      {
        text: "Upsert",
        link: null,
        active: true
      }
    ]
  }

  override initialize(exId: number): void {
    this.loading = true
    this.isUpdate = exId != null
    if (this.isUpdate) {
      const userFormGroup = this.employeeForm.get('user') as FormGroup;
      userFormGroup.removeControl('username')
      userFormGroup.removeControl('password')
    }

    let key = this.componentKey(EventKey.EMPLOYEES.GET(exId))
    this.employeeService.fetchSingle(key, this.config.endpoint, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      this.populateForm(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      console.log(error)
      this.loading = false;
    })

  }

  onSubmit() {
    if (!this.employeeForm.valid) {
      return
    }
    this.upsert(this.exId)
  }

  onImgUploaded(images: IImageSlice[]) {
    if (images.length > 0) {
      /*
      Think about the thumbnail, this will only upload the image, not thumbnail
       */
      this.employeeForm.controls.image.setValue(images[0].image_url)
    }
  }

  getExImages(): IImageSlice[] {
    let image = this.employeeForm.controls.image.value
    if (image == null) {
      return []
    }
    return [{image_url: image, thumb_url: image}]
  }

  getConfig(): BaseConfigV2 {
    return this.config;
  }

  getListPageRoute(): string {
    return selfRoutes.list;
  }

  upsert(exId: number | null) {
    this.loading = true
    let key = this.componentKey(EventKey.EMPLOYEES.UPSERT(exId))

    let req
    if (exId == null) {
      req = this.employeeForm.value as IEmployeeCreateReq
      req.user.name = req.name
      req.user.phone = req.phone
      req.user.email = req.email
      req.user.role_names = ['Salesman']
    } else {
      req = this.employeeForm.value as IEmployeeUpdateReq
    }

    this.employeeService.upsert(key, this.config.endpoint, req, exId)

    this.subscribe(key, null, (value, meta) => {
      this.notifier.success("Success", "Employee has been " + (exId ? "updated" : "created"))
      this.employeeForm.reset();
      this.onSuccess(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      this.notifier.warning('Failed!', error.error.message)
    })
  }

  private populateForm(n: IEmployeeDetail) {
    this.selectedOrg = n.organization
    this.selectedUser = n.user

    this.employeeForm.controls.name.setValue(n.name)
    this.employeeForm.controls.title.setValue(n.title)
    this.employeeForm.controls.description.setValue(n.description)
    this.employeeForm.controls.phone.setValue(n.phone)
    this.employeeForm.controls.email.setValue(n.email)
    this.employeeForm.controls.image.setValue(n.image == null ? null : n.image)
    this.employeeForm.controls.organization_id.setValue(n.organization.id)
    this.employeeForm.controls.user_id.setValue(n.user.id)
  }

  onOrgSelected($event: IOrganization | null) {
    this.selectedOrg = $event
    this.employeeForm.controls.organization_id.setValue(
      this.selectedOrg == null ? -1 : this.selectedOrg.id
    )
  }

  onUserSelected($event: IUserBrief | null) {
    this.selectedUser = $event
    this.employeeForm.controls.user_id.setValue(
      this.selectedUser == null ? -1 : this.selectedUser.id
    )
  }

  onUsernameChange(event: any) {
    let username: string = event.target.value
    if (username.length < 3) {
      return
    }

    let key = this.componentKey(EventKey.USERS.CHECK_USERNAME({username: username}))
    this.userService.checkUsername(key, username)
    this.subscribe(key, null, (value, meta) => {
      this.checkUsernameResponse = meta.data
      // set form control invalid if not available
      if (!this.checkUsernameResponse?.available) {
        this.employeeForm.controls.user.get('username')?.setErrors({invalid: true})
      } else {
        this.employeeForm.controls.user.get('username')?.setErrors(null)
      }
    })
  }
}
