import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {EmployeeComponent} from "./employees/employee.component";
import {EmployeeUpsertComponent} from "./employees/components/employee-upsert/employee-upsert.component";

const routes: Routes = [
  {path: 'employees', component: EmployeeComponent},
  {path: 'employees/create', component: EmployeeComponent},
  {path: 'employees/:id/update', component: EmployeeUpsertComponent},
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class HrRoute {
}
