import {BaseEndpoint} from "../../../../base/endpoints/base.endpoint";
import {IParams} from "../../../../base/endpoints/iparams";
import {environment} from "../../../../../environments/environment";

export class HrEndpoints extends BaseEndpoint {

  static EMPLOYEES = {
    EMPLOYEE_ENDPOINT: environment.base_url + '/api/v1/employees',
    EMPLOYEE_ENDPOINT_ADMIN: environment.base_url + '/api/v1/admin/employees',
    SEARCH(endpoint: string, params: IParams) {
      return endpoint.concat(BaseEndpoint.toParamString(params));
    },
    CREATE(endpoint: string): string {
      return endpoint
    },
    GET(endpoint: string, id: number): string {
      return endpoint + '/' + id
    },
    UPDATE(endpoint: string, id: number): string {
      return endpoint + '/' + id
    },
  }

}
