import {BaseConfigV2} from "../../../../../../base/models/baseConfig";
import {Layout} from "../../../../../common/modules/generic/breadcrumb/breadcrumb.model";
import {HrEndpoints} from "../../hr.endpoints";


export let selfEndpoints = {
  endpoint: HrEndpoints.EMPLOYEES.EMPLOYEE_ENDPOINT,
  adminEndpoint: HrEndpoints.EMPLOYEES.EMPLOYEE_ENDPOINT_ADMIN,
}

export interface EmployeeConfig extends BaseConfigV2 {
}

export let defaultEmployeeConfig: EmployeeConfig = {
  defaultLayout: Layout.GRID,
  upsertOnDialog: true,
  showUserFilter: false,
  showDeleteButton: true,
  endpoint: selfEndpoints.endpoint,
  delEndpoint: selfEndpoints.endpoint
}

export let selfRoutes = {
  list: "/dash/employees",
  create: "/dash/employees/create",
  update(id: number | null) {
    return id == null ? this.create : "/dash/employees/" + id + '/update'
  }
}

